.GameItem {
  margin-bottom: 15px;
  border: 1px solid #ccc;
}

.GameItem .line {
  display: flex;
  padding: 10px;
  margin: 0;
}

.GameItem.fav-level-1 {
  position: relative;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 0, 0, 0.1);
}

.GameItem.fav-level-2 {
  position: relative;
  box-shadow: 0 3px 8px rgba(64, 120, 192, 0.5), 0 0 20px rgba(64, 120, 192, 0.5);
}

.GameSide {
  display: flex;
  align-items: center;
  flex: 1;
}

.GameSide .score {
  width: 40px;
  text-align: right;
  margin-left: 0;
  margin-right: 15px;
  font-size: 14px;
}

.GameSide .score.win {
  font-weight: bold;
  font-size: 16px;
}

.GameSide:first-of-type {
  margin-right: 10px;
}

.GameSide:first-of-type .score {
  text-align: left;
  margin-right: 0;
  margin-left: 15px;
}

.GameItem p {
  margin: 0;
  color: #555;
  font-size: 12px;
  padding: 5px;
  background-color: #f8f8f8;
  border-top: 1px solid #ccc;
}

.GameRecap {
  display: flex;
  align-items: center;
  width: 50px;
  justify-content: flex-end;
}

.GameItem .GameDate {
  padding: 5px;
  font-size: 12px;
  border-bottom: 1px solid #ccc;
  color: #555;
}

.GameBadge {
  color: #55ae2e;
  padding: 3px;
  border-radius: 3px;
}

.GameItem .videoContainer video {
  width: 100%;
}
